import React, { useEffect, useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay } from "swiper/modules";
import TalkToUsNew from "@components/common/talk-to-us-new";

const Banner = (props) => {
  const { db } = props;
  const [isClient, setIsClient] = useState(false);

  useEffect(() => {
    setIsClient(true);
  }, []);

  const slides = [
    "NFT Marketplace",
    "Web3",
    "Crypto Exchange",
    "Blockchain",
    "Game",
    "Metaverse",
    "DeFi",
    "Wallet",
    "Dapp",
  ];
  return (
    <section className="main_hero_sec">
      <Container>
        <Row className="d-flex justify-content-center">
          <Col md={12}>
            <div className="hero_center_div">
              <div className="hero_banner_title">
                <div className="hero_title_sec">
                  {isClient && (
                    <Swiper
                      modules={[Autoplay]}
                      direction={"vertical"}
                      speed={900}
                      autoplay={{
                        delay: 1500,
                        disableOnInteraction: false,
                      }}
                      loop={true}
                      slidesPerView={1}
                      className="mySwiper"
                      breakpoints={{
                        640: {
                          slidesPerView: 1,
                        },
                        768: {
                          slidesPerView: 1,
                        },
                        1024: {
                          slidesPerView: 1,
                        },
                        1400: {
                          slidesPerView: 1,
                        },
                        1800: {
                          slidesPerView: 1,
                        },
                      }}
                    >
                      {slides.map((slide, idx) => (
                        <SwiperSlide key={idx}>
                          <div className="banner_content_sec">
                            <h2>{slide}</h2>
                          </div>
                        </SwiperSlide>
                      ))}
                    </Swiper>
                  )}
                </div>
              </div>
              <h1
                className="stable_text"
                style={{ fontSize: "3rem", fontWeight: "700" }}
              >
                Development Company
              </h1>

              <p>
                Our Team specializes in modernizing and automating industries,
                enterprises, and Businesses with modern digital transformation
                technology solutions like blockchain, AI, Metaverse, Crypto,
                NFT, and Web3.
              </p>
              <div className="main_hero_btns_group">
                <TalkToUsNew label="Get started" isFor="label" db={db} />
              </div>
              <div className="main_hero_first_ellipse"></div>
              <div className="main_hero_second_ellipse"></div>
            </div>
          </Col>
        </Row>
      </Container>
    </section>
  );
};

export default Banner;
